<template>
    <div class="tree_module">
        <el-input class="search_input" v-model="chooseName" @change="showTreeAll" @focus="showTreeAll" style="margin: 2px 0"  :title="chooseName"
                  :disabled="disabled">
            <el-button slot="append" :icon="iconTree" @click="showHideTree" :disabled="disabled"></el-button>
        </el-input>
        <el-input style="display: none" v-model="defaultId"></el-input>
        <div class="tree-container" v-if="showTree" :style="{minWidth:treeWidth+'px',maxHeight:treeHeight+'px'}">
            <el-tree class="tree" highlight-current ref="tree" :default-expanded-keys=expandedArr :data="dataTree"
                     node-key="id" :current-node-key="chooseId" :indent="0" :key="chooseName"
                     @node-click="chooseTreeNode" :props="defaultProps">
                <span class="custom-tree-node" slot-scope="{ node, data }">
                    <i-symbol :iconName="data.icon" :iconWidth="15" :iconHeight="15" style="display: inline"
                              iconColor="#3a7bd3"></i-symbol>
                    <span>{{ node.label }}</span>
               </span>
            </el-tree>
        </div>

    </div>
</template>
<script>
    export default {
        name: "tree",
        props: {
            dataTree: {
                type: Array,
                required: true,
            },
            defaultName: {
                type: String,
                default: '',
            },
            defaultId: {
                type: Number,
                default: 0,
            },
            treeWidth: {
                type: Number,
                default: 300,
            },
            treeHeight: {
                type: Number,
                default: 500,
            },
            disabled: {
                type: Boolean,
                default: false,
            }

        },
        components: {},
        data() {
            return {
                chooseName: '',//选中name
                chooseId: '',//选中id
                iconTree: "el-icon-arrow-up",
                showTree: false,
                expandedArr: [0],//默认展开项目
                defaultProps: {
                    children: 'children',
                    label: 'name',
                }
            }
        },
        mounted() {
            this.chooseName = this.defaultName;
            this.chooseId = this.defaultId;
            // this.showTree=false

        },
        methods: {
            chooseTreeNode: function (node) {
                this.chooseName = node.name;
                this.chooseId = node.id;
            }
            , findValue: function (val, obj) {
                if (obj.length > 0) {
                    for (let i = 0; i < obj.length; i++) {
                        if (obj[i].name.indexOf(val) > -1) {
                            this.expandedArr.push(obj[i].id)
                        }
                        if (obj[i].children) {
                            this.findValue(val, obj[i].children)
                        }
                    }
                }
            }
            , showHideTree: function () {
                if (this.iconTree === "el-icon-arrow-down") {
                    this.iconTree = "el-icon-arrow-up"
                    this.showTree = false;

                } else {
                    this.iconTree = "el-icon-arrow-down"
                    this.showTree = true;
                }
            }
            , showTreeAll: function () {
                this.iconTree = "el-icon-arrow-down"
                this.showTree = true;
            }
        },
        watch: {
            chooseName: function (val) {
                this.expandedArr = [0];
                if (val !== "") {
                    this.findValue(val, this.dataTree)
                    let obj = {};
                    obj.name = val
                    obj.id = this.chooseId;
                    this.$emit("pullNode", obj);//传出节点的name 和id
                }
             },

    }
    }
</script>

<style lang="scss">

    .tree-container {
        overflow-x: hidden;
        background-color: white;
        width: fit-content;


        .el-tree {
            width: fit-content;
            min-width: calc(100% - 2px);
            border: 1px solid lightgrey;
            border-radius: 5px;
            z-index: 300;

        }

        .el-tree--highlight-current .el-tree-node.is-current > .el-tree-node__content {
            color: #5e68ff;
        }
    }

    .tree-container /deep/ .el-tree > .el-tree-node:after {
        border-top: none;
    }

    .tree /deep/ .el-tree-node__content > .el-tree-node__expand-icon {
        padding: 6px;
    }

    .tree /deep/ .el-tree-node__expand-icon.is-leaf {
        width: 1px;
    }

    .tree /deep/ .el-icon-caret-right:before {
        /*content: "";*/
    }

    .tree /deep/ .el-tree-node {
        position: relative;
        padding-left: 16px;
    }

    .tree /deep/ .el-tree-node__children {
        padding-left: 16px;
    }

    .tree /deep/ .el-tree-node :last-child:before {
        height: 38px;
    }

    .tree /deep/ .el-tree > .el-tree-node:before {
        border-left: none;
    }

    .tree-container /deep/ .el-tree > .el-tree-node:after {
        border-top: none;
    }

    .tree /deep/ .el-tree-node:before {
        content: "";
        left: -4px;
        position: absolute;
        right: auto;
        border-width: 1px;
    }

    .tree /deep/ .el-tree-node:after {
        content: "";
        left: -4px;
        position: absolute;
        right: auto;
        border-width: 1px;
    }

    .tree /deep/ .el-tree-node:before {
        border-left: 1px dashed #4386c6;
        bottom: 0px;
        height: 100%;
        top: -26px;
        width: 1px;
    }

    .tree /deep/ .el-tree-node:after {
        border-top: 1px dashed #4386c6;
        height: 20px;
        top: 12px;
        width: 24px;
    }


</style>



