<!--文件图片上传-->
<template>
    <div class="uploadFile">
        <!--图片放大层-->
        <div class="blowUpShow" v-show="isBlowUp">
            <i class="el-icon-circle-close" @click="isBlowUp=false"></i>
            <img id="blowUpImg" :src="imgPath">
        </div>
        <div class="uploadBox" v-for="(item,index) in fileList" :key="index">
            <input type="file" class="inputStyle" :ref="'upload'+index" :id="'upload'+index"
                   @change="uploadFile(index,item)"
                   :style="outBorder">
            <div class="showImg" :style="outBorder">
                <img :ref="'img'+index" :src="item.src" :width="item.width" :height="item.height"
                     :style="{padding:item.padding}" class="imgPrev">
            </div>
            <!--操作按钮-->
            <div class="operateImg" :style="outBorder">
                <i v-show="item.fileType===''" class="el-icon-circle-plus-outline"
                   @click="imgAdd(index)"></i>
                <i v-show="item.fileType=='image'" class="el-icon-zoom-in"
                   @click="imgBlowUp(index)"></i>
                <i v-show="item.fileType!==''" class="el-icon-delete"
                   @click="imgDelete(index)"></i>
            </div>
            <!-- 显示文件名-->
            <div v-show="item.name"
                 style="text-align: center;position:relative;top:-244px;left:0px;font-size: 12px;padding:0 10px"
                 :title="item.name"><p style="overflow: hidden;white-space: nowrap">{{item.name}}</p>
            </div>
        </div>

    </div>
</template>
<script>
    import tools from "../../src/utils/tools"

    export default {
        name: "imgPrev",
        props: {
            maxFileNum: {
                type: Number,
                default: 5,
            },
            uploadType: {
                type: String,
                default: "all",
            }
        },

        data() {
            return {
                isBlowUp: false,
                imgPath: "",
                outBorder: {
                    width: "118px",
                    height: "118px",
                    overflow: "hidden",
                    borderRadius: "5px"
                },
                fileNameList: [],
                fileList: [
                    {
                        src: "../../static/img/whiteBgd.jpg",
                        name: '',
                        padding: '0',
                        fileType: '',
                        width: "100%",
                        height: "100%"
                    }
                ],
                //文件最大数量
                uploadData: new FormData()
            };
        },
        created() {
            console.log(this.uploadType)
        },
        mounted() {
        },
        methods: {
            uploadFile: function (n) {
                //判断是否支持FileReader
                var _this = this;
                if (window.FileReader) {
                    var reader = new FileReader();
                }
                var file = _this.$refs["upload" + n][0].files[0];
                console.log(file)
                var imageType = /^image\//;
                var excelType = /(.xls|.xlsx)$/
                var wordType = /(.doc|.docx)/
                //上传文件类型 图片image 文件file 文件图片all
                if (_this.uploadType === "image" | _this.uploadType === "all") {
                    if (imageType.test(file.type)) {
                        //读取完成
                        reader.onload = function (e) {
                            //获取图片dom
                            //图片路径设置为读取的图片
                            if (_this.fileList.length < _this.maxFileNum + 1) {
                                let newVal = {
                                    src: e.target.result,
                                    width: "100px",
                                    height: "100px",
                                    padding: '10px',
                                    name: file.name,
                                    fileType: "image",
                                }

                                if (_this.fileNameList.indexOf(newVal.name) < 0) {
                                    _this.fileList.unshift(newVal);
                                    _this.fileNameList.push(newVal.name);
                                }
                                // 限制上传文件数目
                                if (_this.fileList.length == _this.maxFileNum + 1) {
                                    _this.fileList.splice(-1, 1);
                                    tools.warnNotice("最大上传数量为" + _this.maxFileNum);
                                }
                                console.log(file)
                            } else {

                                return false;
                            }
                        };
                        //result 读取为data开头的url字符串
                        reader.readAsDataURL(file);
                        reader.onloadend = function (e) {
                            //  设置图片大小
                            let img = document.getElementsByClassName("imgPrev");
                            if (img[0]) {
                                _this.fileList[0].imgWidth = img[0].width;
                                _this.fileList[0].imgHeight = img[0].height;
                                if (img[0].width < img[0].height) {
                                    _this.fileList[0].width = "auto";
                                    _this.fileList[0].height = "100%";
                                } else {
                                    _this.fileList[0].width = "100%";
                                    _this.fileList[0].height = "auto";
                                }
                            }
                        };
                    } else {
                        if (_this.fileType === "image") {
                            tools.warnNotice("请选择图片")
                        }
                    }
                }
                if (_this.uploadType === "file" | _this.uploadType === "all") {
                    if (excelType.test(file.name)) {
                        reader.readAsDataURL(file);
                        //读取完成
                        reader.onload = function (e) {
                            console.log(e)
                            if (_this.fileList.length < _this.maxFileNum + 1) {
                                let newVal = {
                                    src: '../../static/img/excel.png',
                                    width: "100px",
                                    height: "100px",
                                    padding: '10px',
                                    name: file.name,
                                    fileType: "excel",
                                }
                                if (_this.fileNameList.indexOf(newVal.name) < 0) {
                                    _this.fileList.unshift(newVal);
                                    _this.fileNameList.push(newVal.name);
                                }
                                // 限制上传文件数目
                                if (_this.fileList.length == _this.maxFileNum + 1) {
                                    _this.fileList.splice(-1, 1);
                                    tools.warnNotice("最大上传数量为" + _this.maxFileNum);
                                }
                            } else {
                                return false;
                            }
                        };
                    } else if (wordType.test(file.name)) {
                        reader.readAsDataURL(file);
                        //读取完成
                        reader.onload = function (e) {
                            console.log(e)
                            if (_this.fileList.length < _this.maxFileNum + 1) {
                                let newVal = {
                                    src: '../../static/img/word.png',
                                    width: "100px",
                                    height: "100px",
                                    padding: '10px',
                                    name: file.name,
                                    fileType: "word",
                                }
                                if (_this.fileNameList.indexOf(newVal.name) < 0) {
                                    _this.fileList.unshift(newVal);
                                    _this.fileNameList.push(newVal.name);
                                }
                                // 限制上传文件数目
                                if (_this.fileList.length == _this.maxFileNum + 1) {
                                    _this.fileList.splice(-1, 1);
                                    tools.warnNotice("最大上传数量为" + _this.maxFileNum);
                                }
                            } else {
                                return false;
                            }
                        };
                    } else {
                        if (_this.fileType === "file") {
                            tools.warnNotice("请选择文件")
                        }
                    }
                }
                _this.uploadData.append("name" + n, file)

            },
            // 添加
            imgAdd: function (n) {
                let _this = this;
                document.getElementsByClassName("inputStyle")[0].click();
                _this.$axios
                        .post("127.0.0.1", _this.uploadData)
                        .then(function (response) {
                            console.log(response);
                        })
                        .catch(function (error) {
                            console.log(error);
                        });
            },
            // 删除
            imgDelete: function (n) {
                this.fileList.splice(n, 1);
                if(this.fileList.length===0){
                    this.fileList.push({
                        src: "../../static/img/whiteBgd.jpg",
                        name: '',
                        padding: '0',
                        fileType: '',
                        width: "100%",
                        height: "100%"
                    });
                }
                if (this.fileList[this.fileList.length - 1].name !== "") {
                    this.fileList.push({
                        src: "../../static/img/whiteBgd.jpg",
                        name: '',
                        padding: '0',
                        fileType: '',
                        width: "100%",
                        height: "100%"
                    });
                }
            },
            imgBlowUp: function (n) {
                this.isBlowUp = true;
                this.imgPath = this.fileList[n].src;
            },
        },
        components: {}
    };
</script>
<style lang="scss">
    .uploadFile {
        .uploadBox {
            display: inline-block;
            width: 120px;
            height: 140px;
            margin: 10px 0 0 10px;
            overflow: hidden;
        }

        .inputStyle {
            display: inline-block;
            background-color: red;
            opacity: 0;
            position: relative;
            vertical-align: top;
            z-index: 200;
        }

        .showImg {
            display: inline-flex;
            background-color: rgb(228, 228, 240);
            border: 1px solid lightgrey;
            vertical-align: top;
            font-size: 40px;
            position: relative;
            top: -118px;
            justify-content: center;
            align-items: center;
        }

        .showImg > img {
            display: inline-block;
            flex: 1;
        }

        .showImg > i {
            display: inline-block;
        }

        .operateImg {
            line-height: 120px;
            text-align: center;
            position: relative;
            color: rgba(9, 8, 144, 0.59) !important;
            top: -240px;
            font-size: 30px;
            /*color: rgba(186, 190, 192, 0.425);*/
            z-index: 300;
        }

        .blowUpShow {
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: rgb(233, 233, 240);
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            z-index: 400;
        }

        .blowUpShow > img {
            max-width: 100vw;
        }

        .blowUpShow > i {
            width: 40px;
            font-size: 30px;
            position: absolute;
            top: 10px;
            right: 25px;
            z-index: 400;
            color: rgb(192, 185, 185);
        }

        .btnUpload {
            width: 100%;
            height: 80px;
            line-height: 80px;
        }
    }

</style>



