import {Message, MessageBox} from 'element-ui';

var tools = {
    //删除提示
    deleteConfirm:function(callback){
        MessageBox.confirm("此操作将永久删除, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            closeOnClickModal: true,
            type: "warning",
            center: true
        })
                .then(() => {
                    callback();
                    Message({
                        type: "success",
                        message: "删除成功!"
                    });
                })
                .catch(() => {
                    Message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
    },
    // 警告消息提示
    warnNotice:function(mes){
        Message({
            type:"warning",
            message:mes,
        })
    }
}

export default tools



